export default {
	data() {
		return {
			info: {
				nickName: null
			}
		};
	},
	methods: {
		bindGetUserInfo({ detail }) {
			console.log(detail)
			this.info = { ...JSON.parse(detail.rawData) }
			this.$store.commit("user/SET_USER_INFO", this.info)
		}
	},
	onShow() {
		try {
			this.info = this.$store.getters.userInfo
		} catch (e) {
			console.log(e)
			//TODO handle the exception
		}
	}
}
